.hero {
  background:
    linear-gradient(
      90deg,
      $color-resolution-blue 0%,
      $color-resolution-blue 0.01%,
      #1a3578 39.11%,
      #73184b 75.5%,
      #b4022a 100%
    );
}

.hero__container {
  display: grid;
  grid-template-columns: 35% 65%;
  gap: 50px;
  margin: 0 auto;
  padding-top: 0;

  @include vp-1365 {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 0;
  }
}

.hero__text-wrapper {
  display: grid;
  grid-template-columns: 30% 70%;
  gap: 50px 0;
  max-width: 385px;
  color: $color-default-white;
  text-align: right;
  padding: 60px 0 62px;

  @include vp-1365 {
    padding-top: 30px;
    padding-bottom: 31px;
    grid-template-columns: 35% 65%;
  }

  @include vp-767 {
    grid-template-columns: 39% 61%;
    padding-top: 36px;
    padding-bottom: 0;
    gap: 60px 0;
  }
}

.hero__list {
  grid-column: 1/2;
  grid-row: 1/3;
  margin: 0;
  padding: 0;
  list-style: none;
  align-self: center;
  padding-top: 3px;
  padding-right: 2px;

  @include vp-1365 {
    padding-right: 0;
    padding-top: 0;
  }

  @include vp-767 {
    line-height: 20px;
    padding-top: 7px;
  }
}

.hero__title-area {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  word-break: break-word;
}

.hero__item {
  display: block;
}

.hero__title {
  font-family: $ff-alumni;
  font-size: 92px;
  font-style: normal;
  font-weight: 800;
  line-height: 110px;
  letter-spacing: 4.6px;
  text-transform: uppercase;
  margin: 0;
  padding-top: 11px;

  @include vp-1365 {
    font-size: 72px;
    line-height: 85px;
    letter-spacing: normal;
  }

  @include vp-767 {
    font-size: 62px;
    line-height: 70px;
    letter-spacing: 1.86px;
  }
}

.hero__description {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 54px;
  line-height: 54px;
  letter-spacing: 1.62px;
  text-transform: uppercase;
  margin: 0;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    padding-bottom: 4px;
  }
}

.hero__button {
  grid-row: 2/3;
  grid-column: 2/3;
  padding-left: 54px;
  padding-right: 54px;
  margin-left: auto;
  margin-bottom: auto;

  @include hover-focus {
    color: $color-egyptian-blue;
    background-color: $color-default-white;
  }

  @include active {
    color: $color-sapphire;
    background-color: $color-default-white;
  }

  &:disabled,
  &--disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @include vp-1365 {
    padding: 20px 33px;
  }

  @include vp-767 {
    grid-column: 1/3;
    padding: 20px;
    width: 290px;
  }
}

.hero__image-wrapper img {
  object-fit: cover;

  @include vp-1365 {
    width: 324px;
    height: 390px;
  }
}

.hero__image-wrapper {
  @include vp-767 {
    display: none;
  }
}
