/* alumni-sans-regular - latin */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Alumni Sans";
  font-display: swap;
  src:
    url("../fonts/alumni-sans-v18-cyrillic_latin-regular.woff2")
    format("woff2");
}

