.features {
  background-color: #f8f9fc;
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: center;
  padding-left: 163px;
  padding-right: 163px;
  padding-bottom: 118px;

  @include vp-1365 {
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    gap: 55px;
    padding-bottom: 50px;
  }
}

.features h2 {
  font-family: $ff-alumni;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  text-align: center;
  margin: 0;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
  }
}

.features ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: dense;
  gap: 82px 10px;

  @include vp-1365 {
    grid-template-columns: repeat(2, 1fr);
    gap: 40px 50px;
    grid-auto-flow: row;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }
}

.features li {
  display: flex;
  gap: 30px;
  min-height: 92px;

  @include vp-1365 {
    gap: 15px;
    min-height: 92px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 10px;
    align-items: center;
    text-align: center;
    min-height: 172px;
    padding: 0 45px;
  }
}

.features li:nth-child(5n + 3) {
  grid-row: span 2;
  flex-direction: column;
  justify-content: center;
  gap: 12px;
  align-items: center;
  text-align: center;
  color: $color-default-white;
  background-color: #1c3374;
  min-height: 340px;
  margin: 4px 50px 0;
  padding: 34px;

  @include vp-1365 {
    grid-column: 1/-1;
    width: 400px;
    min-height: 262px;
    margin: 0 auto;
    padding: 35px 35px 26px;
    gap: 25px;
  }

  @include vp-767 {
    width: 100vw;
    min-width: 320px;
    min-height: 225px;
    gap: 5px;
    padding: 30px 70px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
}

.features li:nth-child(5n + 2) {
  grid-column: 3;

  @include vp-1365 {
    grid-column: unset;
  }
}

.features li > p {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  color: #2e35ac;
  display: flex;
  flex-direction: column;
  max-width: 110px;

  @include vp-767 {
    line-height: 20px;
  }
}

.features span {
  font-size: 70px;
  font-style: normal;
  font-weight: 700;
  line-height: 70px;
  background: linear-gradient(128deg, #d10a42 0%, #0041ca 45.58%);
  color: $color-sapphire;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
  }
}

.features li:nth-child(5n + 3) span {
  background: $color-default-white;
  color: $color-default-white;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.features li:nth-child(5n + 3) p {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  padding-bottom: 8px;

  @include vp-767 {
    font-size: 14px;
    font-weight: 300;
    line-height: 22px;
  }
}

.features li:nth-child(5n + 3) h3 {
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }
}

.features div {
  display: flex;
  flex-direction: column;
  gap: 8px;

  @include vp-1365 {
    gap: 12px;
  }
}

.features li:nth-child(5n + 3) div {
  gap: 33px;
  word-break: break-word;

  @include vp-1365 {
    gap: 25px;
    padding: 0 60px;
  }

  @include vp-767 {
    gap: 20px;
    padding: 0;
  }
}

.features h3 {
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
  word-break: break-word;

  @include vp-1365 {
    font-size: 20px;
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 18px;
  }
}

.features p {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px;
  margin: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    word-break: break-word;
  }
}

.features li:nth-child(n + 4) {
  min-height: 172px;
  border-top: 2px solid rgba(209, 10, 66, 0.2);
  padding-top: 54px;

  @include vp-1365 {
    border-top: none;
    padding-top: 0;
    min-height: 92px;
  }

  @include vp-767 {
    min-height: 172px;
  }
}

.features br {
  display: none;

  @include vp-767 {
    display: inline;
  }
}
